<template>
  <div class="game-scene" :class="{'game-scene__win': isUserWin, 'game-scene__lose': !isUserWin}">

    <AppPopUp :text="textResult" :img-url="isUserWin  ?
    '/evil_goblin//img/frames/frame-success.png' :
    '/evil_goblin//img/frames/frame-fail.png'"/>

    <AppScope/>


    <AppButtonGroup>
      <AppButton class="button-group__button_green"
                 @click="nextLevel()">Далее</AppButton>
    </AppButtonGroup>
    <canvas ref="goblin" class="goblin" >
      Обновите браузер, к сожалению Ваша версия браузера не поддреживается 😿
    </canvas>

  </div>
</template>

<script>
import resource from '@/helper/Resource';
import EvilGoblin from '@/models/EvilGoblin';
import AppScope from '@/components/common/AppScope.vue';
import AppPopUp from '@/components/common/AppPopUp';
import AppButtonGroup from '@/components/common/AppButtonGroup';
import AppButton from '@/components/common/AppButton';

export default {
  name: 'LevelFinish',
  components: {
    AppButton, AppButtonGroup, AppPopUp, AppScope,
  },
  props: {
    levelFinish: {
      type: Boolean,
      require: true,
    },
  },
  data() {
    return {
      frameId: null,
      canvas: null,
      context: null,
      lastTime: null,
      gameTime: 0,
      speed: 300,
      goblin: new EvilGoblin({ position: [-10, -10] }),
    };
  },

  computed: {
    isUserWin() {
      return this.$store.state.scope > 0;
    },
    textResult() {
      if (this.isUserWin) {
        return 'Ты справилась с заданием гоблина, и ему ничего не остается, '
          + 'как пропустить тебя к следующим испытаниям. «Но не радуйся слишком рано,'
          + ' - злобно шипит Гоблин тебя в след, - мы с тобой ещё встретимся, и тогда посмотрим, кто кого!';
      }
      return 'Увы, ты не справился с заданием гоблина. Но внезапно в его холодное, '
          + 'злобное сердце закралась доброта: Гоблин соглашается пропустить тебя. «Но смотри, - '
          + 'ворчит он, - не разочаруй меня: со следующими заданиями ты должен совладать гораздо лучше.';

      return this.$store.state.scope > 0;
    },
  },
  watch: {
    canvas() {
      this.goblin.updateDefaultState('repeatFinish');
    },
  },
  methods: {
    init() {
      const { $refs } = this;
      this.canvas = $refs.goblin;
      const { canvas } = this;
      if (canvas.getContext) {
        canvas.width = 350; // goblin size
        canvas.height = 350;
        this.context = canvas.getContext('2d');
      }
      this.setGoblin();
      this.updateGame();
    },

    nextLevel() {
      this.$emit('startGame');
    },

    setGoblin() {
      this.goblin.animate(this.context);
    },

    updateGame() {
      if (this.levelFinish) {
        return;
      }
      const now = Date.now();
      const dt = (now - this.lastTime) / 1000.0;

      this.update(dt);

      this.lastTime = now;
      this.frameId = window.requestAnimationFrame(this.updateGame);
    },
    update(dt) {
      const { context, canvas } = this;
      this.gameTime += dt;

      context.clearRect(0, 0, canvas.width, canvas.height);
      this.setGoblin();
    },
  },
  mounted() {
    resource.onReady(this.init);
  },
  beforeDestroy() {
    cancelAnimationFrame(this.frameId);
  },
};
</script>

<style lang="scss" scoped>

  .game-scene {
    flex-grow: 1;
    background-repeat: no-repeat;
    background-position: center center;
    &__win {
      background-image: url("/evil_goblin//img/bg/bg-5.jpg");
    }
    &__lose {
      background-image: url("/evil_goblin//img/bg/bg-3.jpg");
    }
  }

  .goblin {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 350px;
    height: 350px;
  }

  .button-group {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    &__button {
      color: white;
      border: 0;
      padding: 10px;
      margin: 0 5px 10px 5px;
      min-width: 200px;
      font-size: 18px;
      border-radius: 10px;
    }

    &__button_green {
      background-color: green;
    }
  }
</style>
