<template>
  <div class="game">
    <transition>
      <LevelOne v-if="!levelFirstFinish"
                :level-finish="levelFirstFinish"
                @levelFinish="levelFirstFinish = true"/>

      <div v-else-if="finishDemo">
        <AppPopUp
          text="Демо версия подошла к концу,
        спасибо за уделенное время.Злой гоблин пошел готовится к
        новым заданиям и Вам советую...Гоблин так просто не отступит"
          img-url="/evil_goblin//img/frames/frame-success.png"></AppPopUp>
        <AppButtonGroup>
          <AppButton class="button-group__button_green"
                     @click="startNewGame">Начать с начала
          </AppButton>
        </AppButtonGroup>
      </div>

      <LevelFinish v-else @startGame="endGame"/>
    </transition>
  </div>
</template>

<script>
import json from '@/json/game_1.json';
import resource from '@/helper/Resource';
import LevelOne from '@/components/LevelOne.vue';
import LevelFinish from '@/components/LevelFinish.vue';
import AppPopUp from '@/components/common/AppPopUp.vue';
import AppButtonGroup from '@/components/common/AppButtonGroup.vue';
import AppButton from '@/components/common/AppButton.vue';

export default {
  name: 'GameGround',
  components: {
    AppButton,
    AppButtonGroup,
    AppPopUp,
    LevelFinish,
    LevelOne,
  },
  data() {
    return {
      levelFirstFinish: false,
      finishDemo: false,
    };
  },

  methods: {
    endGame() {
      this.finishDemo = true;
    },
    startNewGame() {
      this.levelFirstFinish = false;
      this.$store.commit('clearScope');
    },
  },
  mounted() {
    const urls = json.map(data => data.img);
    resource.load([
      '/evil_goblin//img/bg/bg-1.jpg',
      '/evil_goblin//img/puzzle/frame-puzzle.png',
      '/evil_goblin//img/gnom/goblin_angry_1.png',
      '/evil_goblin//img/gnom/goblin_angry_2.png',
      '/evil_goblin//img/gnom/goblin_angry_3.png',
      '/evil_goblin//img/gnom/goblin_claps_1.png',
      '/evil_goblin//img/gnom/goblin_claps_2.png',
      '/evil_goblin//img/gnom/goblin_happy_1.png',
      '/evil_goblin//img/gnom/goblin_happy_2.png',
      '/evil_goblin//img/gnom/goblin_repeat_1.png',
      '/evil_goblin//img/gnom/goblin_repeat_2.png',
      '/evil_goblin//img/gnom/goblin_default_1.png',
      '/evil_goblin//img/gnom/goblin_repeatFinish_1.png',
      ...urls,
    ]);
  },
};
</script>

<style lang="scss" scoped>
  .game {
    position: relative;
    display: flex;
    flex-grow: 1;
    overflow: hidden;
  }

</style>
